import {
	getLogIn,
	getAllList
} from '../../api/api.js'
import yangrMsg from "@/components/yangr-msg/yangr-msg.vue"
import {
	topBar
} from "@/pages/topBar/topBar.vue"
import uniCard from '@/components/uni-card/uni-card.vue'

export default {
	components: {
		yangrMsg,
		topBar,
		uniCard
	},
	data() {
		return {
			page: 1,
			total: 0,
			alertTitle: '发生错误',
			alertInfo: '登陆失败！',
			yangrMsgShow: false,
			title: '热门话题',
			secondTitle: '精选推荐',
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			bannerList: [{
				bg: require('../../static/image/ad1.jpg')
			}, {
				bg: require('../../static/image/ad2.jpg')
			}, {
				bg: require('../../static/image/ad3.jpg')
			}],
			imgList: [{
				bg: require('../../static/image/nav1.jpg'),
				text: '不一样的清新BGM'
			}, {
				bg: require('../../static/image/nav2.jpg'),
				text: '【纯音乐】萦绕的忧伤'
			}, {
				bg: require('../../static/image/nav3.jpg'),
				text: '加油，武汉！'
			}],
			contentList: []
		}
	},
	filters: {
		ellipsis(value, num) {
			if (!value || !num) return "";
			if (value.length >= num) {
				return value.slice(0, num) + "...";
			}
			return value;
		}
	},
	created() {
		this.getAllList()
	},
	methods: {
		closeYangrMsg() {
			this.yangrMsgShow = false;
		},
		details(index) {
			console.log(index)
		},
		getAllList() {
			getAllList({page: this.page}).then(res => {
				if (res.data.status === 'success') {
					this.total = res.data.total
					this.contentList.push(...res.data.data)
				} else {
					this.yangrMsgShow = true
				}
			}).catch(err => {
				console.log(err)
				this.yangrMsgShow = true
			})
		},
		getLogIn() {
			let params = {
				userName: 'admin',
				passWord: '123456'
			};
			getLogIn(params).then(res => {
				if (res.data.status === 'success') {
					console.log(res)
				} else {
					this.yangrMsgShow = true
				}
			}).catch(err => {
				console.log(err)
				this.yangrMsgShow = true
			})
		},
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		},
		scroll(e) {
			console.log(e)
		},
		getMore() {
			this.page++
			this.getAllList()
		}
	}
}
